.steps :global(.ant-steps-item-title) {
  padding: 0;
  margin-bottom: 3px;
  font-size: var(--font-size-default) !important;
  display: flex;
  justify-content: center;
  font-weight: 500;
  color: var(--font-color);
}
.steps :global(.ant-steps-item-title) :global(.ant-steps-item-subtitle) {
  color: var(--font-color-grey-1);
  font-size: var(--font-size-small);
  font-weight: 400;
}
.steps :global(.ant-steps-item-description) {
  font-size: var(--font-size-small);
  color: var(--font-color-grey);
}
.steps :global(.ant-steps-item-description) :global(.ant-space),
.steps :global(.ant-steps-item-description) :global(.ant-typography) {
  font-size: var(--font-size-small);
}
.steps :global(.ant-steps-item) > :global(.ant-steps-item-container) > :global(.ant-steps-item-content) > :global(.ant-steps-item-title) {
  color: var(--font-color);
  line-height: 20px;
}
.steps :global(.ant-steps-item) > :global(.ant-steps-item-container) > :global(.ant-steps-item-content) > :global(.ant-steps-item-description) {
  color: var(--font-color-grey);
  line-height: 16px;
}
.steps:global(.ant-steps-vertical) > :global(.ant-steps-item) :global(.ant-steps-item-title) {
  justify-content: space-between;
}
.steps:global(.ant-steps-vertical) > :global(.ant-steps-item) :global(.ant-steps-item-description) {
  padding-bottom: 16px;
}
.steps :global(.ant-steps-item-error) :global(.ant-steps-item-tail)::after {
  background-color: #f0f0f0;
}
.steps :global(.ant-steps-item-error) :global(.ant-steps-item-icon) > :global(.ant-steps-icon) {
  color: #d73737;
}
.steps :global(.ant-steps-item-error) :global(.ant-steps-item-icon) > :global(.ant-steps-icon) :global(.ant-steps-icon-dot) {
  background: #d73737;
}
.steps :global(.ant-steps-item-error) :global(.ant-steps-item-container) > :global(.ant-steps-item-content) > :global(.ant-steps-item-title) {
  color: var(--font-color);
}
.steps :global(.ant-steps-item-wait) :global(.ant-steps-item-container) > :global(.ant-steps-item-content) > :global(.ant-steps-item-title),
.steps :global(.ant-steps-item-wait) :global(.ant-steps-item-container) > :global(.ant-steps-item-content) > :global(.ant-steps-item-description) {
  color: var(--font-color-grey-2);
}
.steps:not(.steps-dot) :global(.ant-steps-icon) {
  display: none;
}
.steps:not(.steps-dot) :global(.ant-steps-item-icon) {
  position: relative;
  border: none;
}
.steps:not(.steps-dot) :global(.ant-steps-item-finish) :global(.ant-steps-item-icon)::before {
  display: block;
  font-family: "iconfont";
  content: "\e679";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: var(--primary-color);
}
.steps:not(.steps-dot) :global(.ant-steps-item-error) :global(.ant-steps-item-icon)::before {
  display: block;
  font-family: "iconfont";
  content: "\e67c";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #d73737;
  background-color: #fff;
}
.steps:not(.steps-dot) :global(.ant-steps-item-wait) :global(.ant-steps-item-icon) {
  border: 1px solid var(--font-color-grey-2);
}
.steps:not(.steps-dot) :global(.ant-steps-item-wait) :global(.ant-steps-item-icon) > :global(.ant-steps-icon) {
  color: var(--font-color-grey-2);
}
.steps:not(.steps-dot) :global(.ant-steps-item-wait) :global(.ant-steps-item-icon) > :global(.ant-steps-icon) .ant-steps-icon-dot {
  color: var(--font-color-grey-2);
}
.steps:not(.steps-dot) :global(.ant-steps-item-wait) > :global(.ant-steps-item-container) > :global(.ant-steps-item-content) > :global(.ant-steps-item-title),
.steps:not(.steps-dot) :global(.ant-steps-item-wait) > :global(.ant-steps-item-container) > :global(.ant-steps-item-content) > :global(.ant-steps-item-description) {
  color: var(--font-color-grey-2);
}
.steps:not(.steps-dot):global(.ant-steps-vertical) :global(.ant-steps-item-process) :global(.ant-steps-item-container) :global(.ant-steps-item-icon) {
  background: var(--primary-color-2);
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
.steps:not(.steps-dot):global(.ant-steps-vertical) :global(.ant-steps-item-process) :global(.ant-steps-item-container) :global(.ant-steps-item-icon):after {
  content: " ";
  width: calc(var(--default-size) / 2);
  height: calc(var(--default-size) / 2);
  background: var(--primary-color);
  display: inline-block !important;
  border-radius: 50%;
}
.steps-default:not(.steps-dot) {
  --default-size: 24px;
}
.steps-default:not(.steps-dot) :global(.ant-steps-item-tail) {
  top: 8px;
}
.steps-default:not(.steps-dot):global(.ant-steps-label-vertical) :global(.ant-steps-item-icon) {
  margin-left: 46px;
}
.steps-default:not(.steps-dot):global(.ant-steps-label-vertical) :global(.ant-steps-item-tail) {
  margin-left: 57px;
  padding: 3.5px 18px;
}
.steps-default:not(.steps-dot):global(.ant-steps-vertical) > :global(.ant-steps-item) > :global(.ant-steps-item-container) > :global(.ant-steps-item-icon) {
  margin-top: 4px;
}
.steps-default:not(.steps-dot):global(.ant-steps-vertical) > :global(.ant-steps-item) > :global(.ant-steps-item-container) > :global(.ant-steps-item-tail) {
  left: 12px;
  padding: 34px 0 0;
}
.steps-default:not(.steps-dot) :global(.ant-steps-item-icon) {
  width: var(--default-size);
  height: var(--default-size);
}
.steps-default:not(.steps-dot) :global(.ant-steps-item-active) :global(.ant-steps-item-icon)::before,
.steps-default:not(.steps-dot) :global(.ant-steps-item-finish) :global(.ant-steps-item-icon)::before {
  font-size: var(--default-size);
}
.steps-default:not(.steps-dot) :global(.ant-steps-item-error) :global(.ant-steps-item-icon)::before {
  font-size: var(--default-size);
}
.steps-small:not(.steps-dot) {
  --default-size: 16px;
}
.steps-small:not(.steps-dot) :global(.ant-steps-item-tail) {
  top: 4px;
}
.steps-small:not(.steps-dot):global(.ant-steps-label-vertical) :global(.ant-steps-item-icon) {
  margin-left: 48px;
}
.steps-small:not(.steps-dot):global(.ant-steps-label-vertical) :global(.ant-steps-item-tail) {
  margin-left: 56px;
  padding: 3.5px 14px;
}
.steps-small:not(.steps-dot):global(.ant-steps-vertical) > :global(.ant-steps-item) > :global(.ant-steps-item-container) > :global(.ant-steps-item-icon) {
  margin-top: 4px;
}
.steps-small:not(.steps-dot):global(.ant-steps-vertical) > :global(.ant-steps-item) > :global(.ant-steps-item-container) > :global(.ant-steps-item-tail) {
  left: 8px;
  padding: 24px 0 0;
}
.steps-small:not(.steps-dot) :global(.ant-steps-item-icon) {
  width: var(--default-size);
  height: var(--default-size);
}
.steps-small:not(.steps-dot) :global(.ant-steps-item-active) :global(.ant-steps-item-icon)::before,
.steps-small:not(.steps-dot) :global(.ant-steps-item-finish) :global(.ant-steps-item-icon)::before {
  font-size: var(--default-size);
}
.steps-small:not(.steps-dot) :global(.ant-steps-item-error) :global(.ant-steps-item-icon)::before {
  font-size: var(--default-size);
}
.steps.steps-dot :global(.ant-steps-item-process) :global(.ant-steps-item-container) :global(.ant-steps-item-icon) {
  width: 8px;
  height: 8px;
}
.steps.steps-dot :global(.ant-steps-item-process) :global(.ant-steps-item-container) :global(.ant-steps-item-icon) :global(.ant-steps-icon-dot) {
  left: 1px;
}
.steps.steps-dot:global(.ant-steps-vertical) :global(.ant-steps-item-icon) {
  margin-top: 8px !important;
}
.steps.steps-dot:global(.ant-steps-vertical) :global(.ant-steps-item) > :global(.ant-steps-item-container) > :global(.ant-steps-item-tail) {
  padding: 6px 0 6px;
}
.steps.steps-dot:global(:not(.ant-steps-vertical)) :global(.ant-steps-item) > :global(.ant-steps-item-container) > :global(.ant-steps-item-tail) {
  top: 3px;
}
.steps.steps-dot:global(:not(.ant-steps-vertical)) :global(.ant-steps-item) > :global(.ant-steps-item-container) > :global(.ant-steps-item-tail)::after {
  height: 1px;
}
.steps.steps-dot.steps-record :global(.ant-steps-item) > :global(.ant-steps-item-container) > :global(.ant-steps-item-tail)::after {
  background-color: #f0f0f0;
}

.step-content-wrap {
  margin-top: 17px;
  transition: all 0.3s;
}
.step-content-wrap :global(.ant-space) {
  width: 100%;
  justify-content: space-between;
}
.step-content-wrap .step-left {
  font-weight: 500;
}
.step-content-wrap .step-left .step-name {
  font-size: var(--font-size-small);
  color: var(--primary-color);
}
.step-content-wrap .step-left .step-action {
  font-size: var(--font-size-small);
  color: var(--font-color);
}
.step-content-wrap .step-action-time {
  font-size: var(--font-size-small);
  color: var(--font-color-grey-1);
}
.step-content-wrap .step-content {
  margin-top: 8px;
  padding: 8px 12px;
  word-break: break-all;
  color: var(--font-color-grey);
  background: var(--bg-color-grey-1);
  border-radius: 0 8px 8px 8px;
  border: 1px solid var(--bg-color-grey-3);
}